@import '~antd/dist/antd.less';
@import './variables.less';

#root {
  height: 100%;
}

.ant-layout.ant-layout-has-sider {
  height: 100%;
}

.ant-layout-sider-zero-width-trigger {
  top: 0px;
  opacity: 0.5;
}

.logo {
  height: 32px;
  margin: 16px;
  background: rgba(255, 255, 255, 0.2);
}

.site-layout-sub-header {
  padding: 0;
  background: #fff;
  height: 48px;
}


.site-layout-content{
  margin: 16px 16px 0;
}

.site-layout-content-block {
  background: #fff;

  padding: 16px;
  min-height: 490px;
  height: calc(100% - 32px);
}


.sentence {

}

.sentence--primary {
  font-size: 38px;
  line-height: 51px;
  font-weight: 700;
}

.sentence--secondary {
  font-size: 28px;
  line-height: 38px;
  color: #888;
}

.wordorder-word {
  font-size: 28px;
  line-height: 38px;
  height: 56px;
}